* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

.container__base {
  max-width: 100%;
  width: 1080px;
  margin-right: auto;
  margin-left: auto;
}
@media screen and (max-width: 500px) {
  .container__base {
    width: 95%;
  }
}